import './card.css'
import React, { Component } from 'react';

class Card extends Component {
    constructor(props){
        super(props);
        this.state={
            ele:JSON.parse(props.data),
            arr:[1,2,3],
        }
        //console.log(this.state.ele)
        var way=document.getElementsByClassName('card');
        console.log(way[0])
    }
    render() {
        //console.log("hello")
        return (
            <div className="hello">
            
            {
            this.state.ele.map((value, index) => ( 
            <div className="card" key={index}>
                {console.log(index)}
                <div className="way"><div className= {"way" + index} ></div></div>
                <div className="time">{value[0]}</div>
                <div className="title">{value[1]}</div>
            </div>
            ))} 
            </div>
        );
    }
}

export default Card;